import React, { useEffect } from "react";
import PropTypes from "prop-types";

const LawmaticsBookingForm = ({ formReference }) => {
  // Ensure the form renders only in the browser
  const isBrowser = typeof window !== "undefined";

  useEffect(() => {
    if (!isBrowser || !formReference) return;

    // Check if the script is already added to the DOM
    const scriptId = `lm-embedded-script-${formReference}`;
    if (!document.getElementById(scriptId)) {
      const script = document.createElement("script");
      script.id = scriptId;
      script.async = true;
      script.innerHTML = `
        !function(e,t,n,a,s,c,i){
          if(!e[s]){
            i=e[s]=function(){
              i.process ? i.process.apply(i,arguments) : i.queue.push(arguments)
            }, i.queue=[], i.t=1*new Date;
            var o=t.createElement(n);
            o.async=1;
            o.src=a+"?t="+Math.ceil(new Date/c)*c;
            var r=t.getElementsByTagName(n)[0];
            r.parentNode.insertBefore(o,r);
          }
        }(window,document,"script","https://navi.lawmatics.com/intake.min.js","lm_intake",864e5);
        lm_intake("${formReference}");
      `;
      document.body.appendChild(script);
    }
  }, [isBrowser, formReference]);

  if (!isBrowser) {
    return null; // Prevent rendering during SSR
  }

  // Render the container for the form
  return <div id={`lm-booking-form-${formReference}`} />;
};

LawmaticsBookingForm.propTypes = {
  formReference: PropTypes.string.isRequired,
};

export default LawmaticsBookingForm;